body
    margin: 0
    padding: 0
    background: #727272
    
#gameshop
    width: 100vw
    height: 100vh
    overflow: hidden
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    background: #242424
    display: none

.gameopen
    position: absolute
    bottom: 5%
    left: 50%
    transform: translateX(-50%)
    padding: 10px 30px
    border-radius: 100px
    border: none
    background: #29aca5
    font-size: 15opx
    color: white
    transition: 0.3s ease
    &:hover
        background: #2b8b87